import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { AboutUsComponent } from './components/about-us/about-us.component';
import { ContactUsComponent } from './components/contact-us/contact-us.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { EmployersComponent } from './components/employers/employers.component';
import { HomeComponent } from './components/home/home.component';
import { PageNotFoundComponent } from './components/page-not-found/page-not-found.component';
import { PortfolioComponent } from './components/portfolio/portfolio.component';
import { ServicesComponent } from './components/services/services.component';
import { UserDetailComponent } from './components/dashboard/user-detail/user-detail.component';
import { PaintingComponent } from './components/painting/painting.component';
import { DesigingComponent } from './components/desiging/desiging.component';
import { TcsComponent } from './components/tcs/tcs.component';
import { WiproComponent } from './components/wipro/wipro.component';

const routes: Routes = [
  { path: '', component: HomeComponent },
  { path:'home', component:HomeComponent },
  { path: 'about', component: AboutUsComponent },
  {
    path: 'services', children: [
      { path: '', component: ServicesComponent },
      { path:'painting', component:PaintingComponent },
      { path:'designing', component:DesigingComponent }
      
    ]
  },
  {
    path: 'clients', children: [
      { path: '', component: EmployersComponent },
      { path:'wipro', component:WiproComponent },
      { path:'tcs', component:TcsComponent }     
    ]
  },
  { path: 'portfolio', component: PortfolioComponent },
  { path: 'contact', component: ContactUsComponent },
  { path: 'dashboard', component: DashboardComponent },
  { path: 'users/:id', component: UserDetailComponent },
  { path: '**', component: PageNotFoundComponent }
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
