import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AppServiceService } from 'src/app/app-services/app-service.service';

@Component({
  selector: 'app-user-detail',
  templateUrl: './user-detail.component.html',
  styleUrls: ['./user-detail.component.scss']
})
export class UserDetailComponent implements OnInit {

  items: any;
  onEdit: any = false;
  constructor(private activatedRout: ActivatedRoute, private appservices:AppServiceService) { }

  ngOnInit(): void {
    
    this.activatedRout.paramMap.subscribe(param => {
      let pid = Number(param.get('id'));
      //console.log(pid);
      this.appservices.fetchSingleUser(pid - 1).subscribe(res => {
        this.items = res;
      });
    });

    this.activatedRout.queryParamMap.subscribe(qParam => {
      console.log(qParam.get('editMode'));
      this.onEdit = qParam.get('editMode');
    })
  }

  updateUsers() {
    console.log(this.items);
  }
}
