import { Component, OnInit } from '@angular/core';
import { ConfirmDialogService } from 'src/app/app-services/confirm-dialog.service';

@Component({
  selector: 'app-confirm-message',
  templateUrl: './confirm-message.component.html',
  styleUrls: ['./confirm-message.component.scss']
})
export class ConfirmMessageComponent implements OnInit {
message: any;
  constructor(private conformDialogue: ConfirmDialogService) { }

  ngOnInit(): void {
      this.conformDialogue.getMessage().subscribe(message => {  
            this.message = message;  
        }); 
  }

}
