import { Component, OnInit } from '@angular/core';
import { AppServiceService } from 'src/app/app-services/app-service.service';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss'],
})
export class HomeComponent implements OnInit {
  bannerData: any;
  subBannerData: any;

  subTitletxt: string =
    'You have to define what kind of index type the object has.';
  constructor(private _dtaservice: AppServiceService) {}

  ngOnInit(): void {
    this.getBannerItems();
  }

  getBannerItems() {
    this._dtaservice.bannerItems().subscribe((data) => {
      this.bannerData = data;
      for (let obj in this.bannerData) {
        //console.log(this.bannerData[obj])
        if (this.bannerData[obj].id == 1) {
          this.bannerData[obj].sub_title = this.subTitletxt;
        }
      }
    });
  }
}
