import {
  Component,
  OnInit,
  AfterViewInit,
  ViewChild,
  ElementRef,
  HostListener,
} from '@angular/core';
import { OverlayShowDirective } from 'src/app/app-directives/overlay-show.directive';
import { AppServiceService } from 'src/app/app-services/app-service.service';
import { Location } from '@angular/common';
import { Router } from '@angular/router';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss'],
})
export class HeaderComponent implements OnInit, AfterViewInit {
  sticky: boolean = false;
  elementPosition: any;
  menuSlide: any;
  overlaydir: any;
  overLay: any;
  logoImg: string = 'assets/images/logo.png';
  toggleIcon: string = 'assets/images/menu.svg';
  orIcon: string = 'assets/images/social-login.jpg';
  navData: any;

  activeElement = 1;
  isLogin: boolean = false;
  isSignup: boolean = true;
  signup: any;
  switchAccount: any;
  route: any;

  @ViewChild('stickyMenu', { static: false }) stickymenu!: ElementRef;
  @ViewChild('slidetoggle', { static: false }) slideToggle!: ElementRef;
  @ViewChild('overlayblock', { static: false }) ovelaBlock!: ElementRef;
  @ViewChild('switch', { static: false }) switch!: ElementRef;
  @ViewChild(OverlayShowDirective, { static: false })
  overlayDir!: OverlayShowDirective;

  constructor(
    private _dtaservice: AppServiceService,
    private location: Location,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getNavs();
  }

  ngAfterViewInit() {
    this.elementPosition = this.stickymenu.nativeElement;
    this.menuSlide = this.slideToggle.nativeElement;
    this.overLay = this.ovelaBlock.nativeElement;
    this.switchAccount = this.switch.nativeElement;
  }

  getNavs() {
    // this._dtaservice.navItems().subscribe(data => this.navData = data);
    this._dtaservice.navItems().subscribe((data) => {
      this.navData = data;
      this.navData.forEach((item: any, index: any) => {
        if (item.id == 3) {
          item.submenu.forEach((subName: any) => {
            if (subName.id == 1) {
              console.log(subName.name);
            }
          });
        }
      });
    });
  }

  // Navs Active class
  selectedItem(id: number) {
    this.activeElement = id;
  }

  @HostListener('window:scroll', ['$event'])
  handleScroll() {
    if (window.scrollY > 1.5) {
      this.elementPosition.classList.add('fixed-top');
    } else {
      this.elementPosition.classList.remove('fixed-top');
    }
  }

  // Onclick mobile menu

  navSlide() {
    this.menuSlide.classList.add('show-nav');
    this.overLay.classList.add('fade-overlay');
  }

  hideOverlay(e: any) {
    if (e.target == this.overLay) {
      this.overLay.classList.remove('fade-overlay');
    }
    this.menuSlide.classList.remove('show-nav');
  }
}
