import { Component, AfterViewInit, ElementRef, OnInit, ViewChild, HostListener, Output, EventEmitter } from '@angular/core';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-login-form',
  templateUrl: './login-form.component.html',
  styleUrls: ['./login-form.component.scss']
})
export class LoginFormComponent implements OnInit, AfterViewInit {
  
  questData: any;
  isOther: boolean = false;


  @ViewChild('loginForm') loginForm !: NgForm;
  @ViewChild('checkother') checkOther !: ElementRef

  selectedValue:string = "Select";
  selected = true;
  showVal: boolean = false;
  selectClass: any;

  

  formData = {
    username: '',
    email: '',
    password: '',
    scureCode: '',
    remember:''
  }

  secureData() {
        return [
          {
            value: '',
            optName: 'Select'
          },
          {
            value: 'Father Name',
            optName: 'Father Name'
          },
          {
            value: 'Mother Name',
            optName: 'Mother Name'
          },
          {
            value: 'Pet Name',
            optName: 'Pet Name'
          },
          {
            value: 'Other',
            optName: 'Other'
          }
        ]
  }
  constructor() { }


  ngAfterViewInit(): void {
    this.selectClass = this.checkOther.nativeElement;
     console.log(this.selectClass )
  }



  ngOnInit(): void {
    // console.log(this.secureData())
    this.getSecure();
  }


  getSecure() {
    this.questData = this.secureData();
    for (let obj in this.questData) {
      let qData = this.questData[obj];
      
      if (qData.value !== "Other") {
        this.isOther = false;
      } else {
        this.isOther = true;
      }
    }
  }
  
  


  onSubmit(form:NgForm) {
    console.log(this.loginForm);
    this.showVal = true;
    this.formData.username = this.loginForm.value.user;
    this.formData.email = this.loginForm.value.email;
    this.formData.password = this.loginForm.value.password;
    this.formData.scureCode = this.loginForm.value.scureCode;

    this.loginForm.reset();
  }

}
function secureData(): any {
  throw new Error('Function not implemented.');
}

