import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { Observable } from 'rxjs';

import { Navs } from '../modals/navs';
import { Banner } from '../modals/banner';
import { Users } from '../modals/users';
import { config } from '../config';

@Injectable({
  providedIn: 'root',
})
export class AppServiceService {
  apiUrl = config.API_URL;
  masterApi = config.MASTER_URL;

  navData: string = `${this.apiUrl}/navigation.json`;
  bannerData: string = `${this.apiUrl}/banner.json`;
  userData: string = `${this.apiUrl}/users.json`;
  masterData: string = `${this.masterApi}`;

  constructor(private http: HttpClient) {}

  // Main Menu
  navItems(): Observable<Navs> {
    return this.http.get<Navs>(this.navData);
  }

  // Home banner slider
  bannerItems(): Observable<Banner> {
    return this.http.get<Banner>(this.bannerData);
  }

  //Add User

  addUser(users: any[]) {
    return this.http.put(this.userData, users);
  }

  // Dashboard Users
  users(): Observable<Users> {
    return this.http.get<Users>(this.userData);
  }

  // Fetch single user details
  fetchSingleUser(id: any) {
    return this.http.get<Users>(`${this.apiUrl}/users/${id}.json`);
  }

  // Delete single user details
  deleteSingleUser(id: any) {
    return this.http.delete<Users>(`${this.apiUrl}/users/${id}.json`);
  }
}
